.aside{
    .cross {
        transition: opacity 1s ease-in-out;
        display: none;
        opacity: 0;
        visibility: hidden;
      }  
    &.open{
        .aside-container::after{
            background: none !important;
        }
        .cross {
            transition:opacity 1s ease-in-out; 
            display: inline-block;
            margin: 0;
            visibility: visible;
            opacity: 1;
        }
        .burger {
            display: none;
        }
        
    }
    
    height: 100vh;
    width: 90px;
    padding: 0.8rem 0;
    position: fixed;
    right: 0;
    z-index: 400;

    

    &-container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        position: relative;
        width: 100%;
        height: 100%;
        transition: height 0.4s linear;
        &::after{
            content: '';
            position: absolute;
            top: -20%;
            right: 0%;
            width: 100%;
            height: 0%;
            background-color: #000;
            opacity: 0.2;
            z-index: -1;
            transition: height 0.4s linear;
        }
        &.change{
            transition: height 0.4s linear !important;
            &::after{
                transition: height 0.4s linear !important;
                height: 200% !important;
            }
        }
              
    }
    &-btn,
    &-btn.close{
        position: absolute;
        top: 15px;
        right: 15px;
        transition: all 0.3s ease;
        transition: all 0.3s ease;
        height: 124px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        z-index: 500;
        &::after{
            display: block;
            content: "меню";
            color: #fff;
            margin-top: 10px;
            font-size: 11px;
            padding: 18px 10px 18px 0px;
            height: auto;
            opacity: 1;
            text-transform: uppercase;
            transform: rotate(-90deg);
            cursor: pointer;
            transition: all 0.3s ease;
        }
        
        &:hover{
            .line {
                background: var(--c-primary);
            }
            svg{
                
                path{
                    fill: var(--c-primary)
                }
            }
            &::after{
                color: var(--c-primary)
            }
        }
        &.open {
            transition: all 0.3s ease;


            #hamburger-1 .line:nth-child(2){
                opacity: 0;
            }
              
              #hamburger-1 .line:nth-child(1){
                transform: translateY(7px) rotate(45deg);
            }
              
              #hamburger-1 .line:nth-child(3){
                transform: translateY(-6px) rotate(-45deg);
            }



            .line {
                background: var(--c-secondary);
            }
            svg{
                path{
                    fill: var(--c-secondary)
                }
            }
            &::after{
                content: 'закрыть';
                color: var(--c-secondary);
            }
            &:hover{
                .line {
                    background: var(--c-primary);
                }
                svg{
                    path{
                        fill: var(--c-primary)
                    }
                }
                &::after{
                    color: var(--c-primary)
                } 
            }
        }
    }

    &-nav-supports{
        position: absolute;
        background-color: var(--c-secondary);
        transition: transform .3s;
        bottom: 50%;
        transform: translateY(50%);
        right: 0;
        width: 100%;

        &.open {
            transform: translate(100%,50%);
            &.tablet-open{
                transform: translate(0%,200%);
            }
        }
        

        &-list {
            margin: 0;
            padding: 0;

            li{
                padding: 20px 10px;
                height: 90px;
                transition: background 0.3s linear;
                a{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    row-gap: 8px;
                    i{
                        color: var(--c-primary);
                        font-size: 21px;
                    }
                    
                    span{
                        font-family: "Monserrat",sans-serif;
                        font-size: 11px;
                        line-height: 1.2;
                        text-align: center;
                    }
                }
                &:hover {
                    background: #3f4f67;
                    
                }
            }
        }
    }
    .toup {
        
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.3s linear;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        padding: 0;
        z-index: 10;
        display: none;
        border-radius: 50%;
        background-color: var(--c-primary);
        opacity: 0;
        margin-bottom: 3px;
        position: relative;
        &::after {
            display: block;
            content: "TOP";
            color: #fff;
            position: absolute;
            font-size: 12px;
            top: -40px;
            left: 14%;
            transform: translateX(-50%);
            height: auto;
            opacity: 1;
            transform: rotate(-90deg);
            cursor: pointer;
            transition: color 0.3s ease;
            
            &:hover{
                color: var(--c-primary)
            }
        }
        &:hover{
            color: var(--c-secondary);

            &::after{
                color: var(--c-primary)
            }
        }
        &.show{
            position: absolute;
            bottom: 0;
            transition: all 0.3s linear;
            display: flex;
            opacity: 1;
        }
    }
}